// section


// text set

.m-text {
  position: relative;
  font-weight: 300;
  font-size: 1.4rem;
  line-height: 2.4rem;
  letter-spacing: 0.1em;
}

.mx-txt {
  position: relative;
  font-weight: normal;
  font-size: 1.8rem;
  @include media('sp') {
    font-size: 1.6rem;
  }
}

.m-title {
  position: relative;
  font-weight: 300;
  font-size: 2.0rem;
  line-height: 3.2rem;
  letter-spacing: 0.1em;
}

.text_list {
  position: relative;
  list-style-type: "・";
  padding-left: 1.8rem;
}

.underline_bg {
  display: inline;
  background: linear-gradient(transparent 68%, #FBB03B 0%);
}

/* btn */

.btn_wrap {
  position: relative;
}

.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 20px;
  max-width: 240px;
  width: 100%;
  height: 50px;
  background: #F2F2F2;
  border: 1px solid #858585;
  font-family: $font_noto;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  color: #1A1A1A;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.4s;
  cursor: pointer;
  @include media('sp') {
    height: 45px;
    max-width: 200px;
  }
  span {
    position: relative;
    color: inherit;
    text-decoration: none;
    z-index: 3;
  }
  &:hover {
    background: #A10707;
    border-color: #A10707;
    color: #FFFFFF;
  }
}

.viewmore_link_wrap {
  position: relative;
  .viewmore_link {
    position: relative;
    display: inline-block;
    font-weight: bold;
    font-style: italic;
    font-size: 16px;
    line-height: 26px;
    color: $orange_color;
    text-decoration: none;
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background: $orange_color;
    }
    &::after {
      content: ">";
      display: inline-block;
      line-height: 1;
      margin-left: 6px;
      vertical-align: text-bottom;
    }
    
    &:hover {
      &::before {
        width: 100%;
        animation: link-lineloop 0.6s ease-out;
        -webkit-animation: link-lineloop 0.6s ease-out;
      }
    }
  }
}

.view-more {
  position: relative;
}

.link-btn {
  position: relative;
  overflow: hidden; 
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.2rem 3.0rem;
  max-width: 100%;
  width: 27.4rem;
  height: 7.0rem;
  background: #FFE138;
  border: 0.3rem solid #FAD400;
  font-family: $font_mincho;
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 300;
  letter-spacing: 0.1em;
  white-space: nowrap;
  color: #000000;
  text-align: center;
  text-decoration: none;
  outline: none;
  border-radius: 3.5rem;
  transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  cursor: pointer;
  z-index: 1;
  span {
    position: relative;
    color: inherit;
    text-decoration: none;
    transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
    z-index: 3;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    background: #FFFFFF;
    width: 100%;
    height: 100%;
    transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
    transform-origin: right top;
    transform: scale(0, 1);
    z-index: 1;
  }
  &:hover {
    color: #000000;
    &::before {
      transform-origin: left top;
      transform: scale(1, 1);
    }
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  outline: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  background: #EEEEEE;
  .modal-dialog {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    margin: 0;
    @include media('sp') {
      min-height: 84.4rem;
    }
  }
  .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 6.0rem 6.0rem 3.0rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    pointer-events: auto;
    z-index: 1;
    @include media('tb') {
      padding: 5.0rem 5.0rem;
    }
    @include media('sp') {
      padding: 4.0rem 3.0rem;
    }
    .product-logo {
      position: absolute;
      top: 6.0rem;
      left: 6.0rem;
      width: 8.2rem;
      z-index: 3;
      @include media('tb') {
        top: 5.0rem;
        left: 5.0rem;
      }
      @include media('sp') {
        top: 5.0rem;
        left: 3.6rem;
      }
    }
    .product-label {
      position: absolute;
      top: 20.0rem;
      left: 6.0rem;
      width: fit-content;
      z-index: 3;
      @include media('tb') {
        top: 19.0rem;
        left: 5.0rem;
      }
      @include media('sp') {
        left: 50%;
        top: auto;
        bottom: 40.6rem;
        width: 100%;
        max-width: 32.8rem;
        transform: translateX(-50%);
      }
      h4 {
        position: relative;
        font-weight: 300;
        font-size: 4.0rem;
        line-height: 1.2;
        letter-spacing: 0.1em;
        text-align: left;
        color: #000000;
        @include media('tb') {
          font-size: 2.4rem;
          line-height: 3.2rem;
        }
        @include media('sp') {
          font-size: 2.0rem;
          line-height: 3.0rem;
        }
      }
      p {
        position: relative;
        font-weight: 300;
        font-size: 1.6rem;
        line-height: 1.5;
        letter-spacing: 0.1em;
        text-align: left;
        color: #000000;
        margin-top: 1.2rem;
        @include media('tb') {
          margin-top: 0.8rem;
        }
        @include media('sp') {
          font-size: 1.4rem;
          line-height: 2.4rem;
          margin-top: 0.3rem;
        }
      }
    }
    .product-slider {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 0;
      .swiper-slide {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 0;
        img {
          position: relative;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .swiper-pagination {
        position: absolute;
        bottom: 4.0rem;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        justify-content: center;
        width: fit-content;
        .swiper-pagination-bullet {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 14.8rem;
          height: 4.5rem;
          background: #C8CBD0;
          padding: 1.0rem 1.2rem 1.0rem 3.0rem;
          font-family: $font_mincho;
          font-size: 1.6rem;
          font-weight: 300;
          line-height: 2.4rem;
          letter-spacing: 0.1em;
          color: #FFFFFF;
          text-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
          text-decoration: none;
          border-radius: 0;
          margin: 0 0.6rem;
          opacity: 1;
          em {
            font-style: normal;
            font-size: inherit;
            color: inherit;
          }
          .jp {
            font-size: 1.6rem;
          }
          .en {
            font-size: 1.4rem;
          }
          small {
            font-size: 1.0rem;
          }
          &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 1.2rem;
            display: block;
            width: 1.2rem;
            height: 1.2rem;
            border-radius: 50%;
            border: 1px solid #FFFFFF;
            filter: drop-shadow(0px 3px 6px rgba($color: #000000, $alpha: 0.16));
            transform: translateY(-50%);
          }
          &.swiper-pagination-bullet-active {
            &::before {
              background: #B1E0F7;
            }
          }
        }
      }
      .swiper-button-prev, .swiper-button-next {
        position: absolute;
        top: 48%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: fit-content;
        height: fit-content;
        cursor: pointer;
        z-index: 10;
        @include media('tb') {
          top: 42%;
        }
        @include media('sp') {
          top: 24%;
          top: calc( ( 100% - 24.0rem ) / 2);
        }
        &::before,
        &::after {
          display: none !important;
        }
      }
      .swiper-button-prev {
        position: absolute;
        left: 6.0rem;
        border-top: 1.5rem solid transparent;
        border-bottom: 1.5rem  solid transparent;
        border-right: 1.5rem solid #000000;
        border-left: 0;
        @include media('tb') {
          left: 5.0rem;
        }
        @include media('sp') {
          left: 1.6rem;
          border-top-width: 1.0rem;
          border-bottom-width: 1.0rem;
          border-right-width: 1.0rem;
        }
      }
      .swiper-button-next {
        position: absolute;
        right: 6.0rem;
        border-top: 1.5rem solid transparent;
        border-bottom: 1.5rem  solid transparent;
        border-left: 1.5rem solid #000000;
        border-right: 0;
        @include media('tb') {
          right: 5.0rem;
        }
        @include media('sp') {
          right: 1.6rem;
          border-top-width: 1.0rem;
          border-bottom-width: 1.0rem;
          border-left-width: 1.0rem;
        }
      }
      .swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
        opacity: 0.7;
      }
    }
    .product-members {
      position: absolute;
      left: 0;
      bottom: 12.0rem;
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      padding: 0;
      width: 100%;
      max-width: 100%;
      z-index: 3;
      @include media('tb') {
        bottom: 15.4rem;
      }
      @include media('sp') {
        left: 50%;
        max-width: 39.0rem;
        width: 100%;
        bottom: 11.0rem;
        transform: translateX(-50%);
      }
      & > li {
        position: relative;
        flex: 0 0 auto;
        width: fit-content;
        padding: 0.6rem 1.0rem;
      }
      .member-thumb {
        position: relative;
        width: 9.6rem;
        cursor: pointer;
        border: 3px solid #FFFFFF;
        box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
        &.active {
          border-color: #FFE138;
        }
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .product-types {
      position: absolute;
      list-style: none;
      bottom: 4.0rem;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
      align-items: center;
      width: fit-content;
      @include media('tb') {
        bottom: 30.0rem;
      }
      @include media('sp') {
        bottom: 34.0rem;
      }
      & > li {
        position: relative;
        &:not(:first-child) {
          margin-left: 2.0rem;
          @include media('tb') {
            margin-left: 3.0rem;
          }
          @include media('sp') {
            margin-left: 1.2rem;
          }
        }
      }
      .type-btn {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 21.0rem;
        height: 4.5rem;
        background: #000000;
        padding: 1.0rem 1.0rem;
        font-family: $font_mincho;
        font-size: 1.4rem;
        font-weight: 300;
        line-height: 2.4rem;
        letter-spacing: 0.05em;
        color: #FFFFFF;
        white-space: nowrap;
        border: 2px solid #000000;
        outline: none;
        text-decoration: none;
        border-radius: 0;
        cursor: pointer;
        margin: 0.2rem 0;
        @include media('tb') {
          font-size: 1.2rem;
          width: 18.0rem;
        }
        @include media('sp') {
          font-size: 1.2rem;
          letter-spacing: 0;
          width: 15.8rem;
        }
        span {
          font-style: normal;
          font-size: inherit;
          color: inherit;
          text-decoration: none;
        }
        .jp {
          font-size: 1.6rem;
          letter-spacing: 0.1em;
          @include media('tb') {
            font-size: 1.4rem;
          }
        }
        .en {
          font-size: 1.4rem;
          letter-spacing: 0.05em;
          @include media('tb') {
            font-size: 1.2rem;
          }
          @include media('sp') {
            letter-spacing: 0;
          }
        }
        small {
          font-size: 1.0rem;
        }
        &.active {
          border-color: #FFE138;
          height: 4.9rem;
          margin: 0;
        }
      }
    }
    .products-btns {
      position: absolute;
      list-style: none;
      right: 8.0rem;
      bottom: 4.0rem;
      display: flex;
      justify-content: flex-end;
      width: fit-content;
      max-width: 100%;
      padding: 0;
      @include media('tb') {
        bottom: 6.0rem;
        right: 50%;
        transform: translateX(50%);
      }
      @include media('sp') {
        bottom: 4.0rem;
      }
      & > li {
        position: relative;
        padding: 0;
        &:not(:first-child) {
          margin-left: 2.0rem;
          @include media('tb') {
            margin-left: 1.2rem;
          }
        }
      }
      .action-btn {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 17.2rem;
        height: 5.6rem;
        font-family: $font_mincho;
        font-weight: 300;
        font-size: 1.6rem;
        letter-spacing: 0.1em;
        line-height: 2.4rem;
        text-align: center;
        color: #FFFFFF;
        white-space: nowrap;
        text-decoration: none;
        background: #2F2F2F;
        padding: 1.0rem 1.0rem;
        border: none;
        outline: none;
        cursor: pointer;
        box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
        @include media('tb') {
          width: 15.8rem;
        }
      }
      .size-select-btn {
        position: relative;
        overflow: hidden;
        padding-right: 1.6rem;
        transition: all 0.4s cubic-bezier(0.8, 0, 0.2, 1);
        z-index: 1;
        span {
          position: relative;
          color: inherit;
          text-decoration: none;
          z-index: 3;
        }
        &::after {
          content: "";
          position: absolute;
          top: 45%;
          top: calc(50% - 4px);
          right: 16px;
          width: 0;
          height: 0;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-top: 8px solid #FFFFFF;
          border-bottom: 0;
          z-index: 3;
        }
      }
      .add-cart-btn {
        position: relative;
        color: #000000;
        background: #FFE138;
        overflow: hidden;
        transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        z-index: 1;
        span {
          position: relative;
          color: inherit;
          text-decoration: none;
          transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
          z-index: 3;
        }
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          background: #2F2F2F;
          width: 100%;
          height: 100%;
          transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
          transform-origin: right top;
          transform: scale(0, 1);
          z-index: 1;
        }
        &:hover {
          color: #FFFFFF;
          &::before {
            transform-origin: left top;
            transform: scale(1, 1);
          }
        }
      }
    }
    .modal-close {
      position: absolute;
      top: 5.0rem;
      right: 5.0rem;
      display: block;
      width: 5.0rem;
      height: 5.0rem;
      cursor: pointer;
      text-decoration: none;
      z-index: 9;
      @include media('tb') {
        top: 5.0rem;
        right: 4.5rem;
      }
      @include media('sp') {
        top: 5.0rem;
        right: 3.0rem;
      }
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: #000000;
        transform: rotate(45deg);
        transform-origin: center center;
        filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16));
      }
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 100%;
        height: 1px;
        background: #000000;
        transform: rotate(-45deg);
        transform-origin: center center;
        filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16));
      }
    }
    .product-prev {
      position: absolute;
      top: 48%;
      left: 6.0rem;
      display: block;
      border-top: 1.5rem solid transparent;
      border-bottom: 1.5rem  solid transparent;
      border-right: 1.5rem solid #000000;
      border-left: 0;
      cursor: pointer;
      z-index: 9;
      @include media('tb') {
        top: 42%;
        left: 5.0rem;
      }
      @include media('sp') {
        top: 24%;
        top: calc( ( 100% - 38.0rem ) / 2);
        left: 2.0rem;
        border-top-width: 1.0rem;
        border-bottom-width: 1.0rem;
        border-right-width: 1.0rem;
      }
      &.disible {
        cursor: not-allowed;
        border-right-color: #D1D1D1;
      }
    }
    .product-next {
      position: absolute;
      top: 48%;
      right: 6.0rem;
      display: block;
      border-top: 1.5rem solid transparent;
      border-bottom: 1.5rem  solid transparent;
      border-left: 1.5rem solid #000000;
      border-right: 0;
      cursor: pointer;
      z-index: 9;
      @include media('tb') {
        top: 42%;
        right: 5.0rem;
      }
      @include media('sp') {
        top: 24%;
        top: calc( ( 100% - 38.0rem ) / 2);
        right: 2.0rem;
        border-top-width: 1.0rem;
        border-bottom-width: 1.0rem;
        border-left-width: 1.0rem;
      }
      &.disible {
        cursor: not-allowed;
        border-left-color: #D1D1D1;
      }
    }
    .select-size-layout {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: none;
      background: transparent;
      backdrop-filter: blur(30px);
      --webkit-backdrop-filter: blur(30px);
      background-color: rgba($color: #000000, $alpha: 0.16);
      overflow-y: auto;
      z-index: 19;
      .layout-content {
        position: relative;
        width: 100%;
        padding: 20.0rem 6.0rem 18.0rem;
        z-index: 0;
        @include media('sp') {
          padding: clamp(12.0rem, 24vh, 20.0rem) 3.0rem clamp(10.0rem, 20vh, 18.0rem);
        }
        .layout-close {
          position: absolute;
          top: 5.0rem;
          right: 5.0rem;
          display: block;
          width: 5.0rem;
          height: 5.0rem;
          cursor: pointer;
          text-decoration: none;
          z-index: 9;
          @include media('tb') {
            top: 5.0rem;
            right: 4.5rem;
          }
          @include media('sp') {
            top: 5.0rem;
            right: 3.0rem;
          }
          &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            display: block;
            width: 100%;
            height: 1px;
            background: #FFFFFF;
            transform: rotate(45deg);
            transform-origin: center center;
            filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16));
          }
          &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            display: block;
            width: 100%;
            height: 1px;
            background: #FFFFFF;
            transform: rotate(-45deg);
            transform-origin: center center;
            filter: drop-shadow(0px 3px 6px rgba(0, 0, 0, 0.16));
          }
        }
        .layout-label {
          position: relative;
          font-weight: 300;
          font-size: 5.0rem;
          letter-spacing: 0.1em;
          line-height: 1.2;
          text-align: center;
          color: #FFFFFF;
          margin-bottom: 8.0rem;
        }
        .size-items {
          position: relative;
          list-style: none;
          width: fit-content;
          max-width: 100%;
          padding: 0;
          margin-left: auto;
          margin-right: auto;
          & > li {
            position: relative;
            &:not(:last-child) {
              margin-bottom: 2.4rem;
            }
          }
          .size-item {
            position: relative;
            display: block;
            width: 100%;
            font-family: $font_mincho;
            font-weight: 300;
            font-size: 3.0rem;
            letter-spacing: 0.1em;
            line-height: 1.2;
            text-align: left;
            color: #FFFFFF;
            text-decoration: none;
            outline: none;
            background: transparent;
            border: none;
            padding: 0 0 0 5.0rem;
            margin: 0;
            cursor: pointer;
            z-index: 0;
            &.active {
              &::before {
                content: "✔︎";
                position: absolute;
                top: -1px;
                left: 0;
                line-height: inherit;
                color: inherit;
              }
            }
          }
        }
      }
    }
  }
  .close {
    position: absolute;
    top: -1.0rem;
    right: -1.0rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.2rem 0.6rem;
    width: 3.0rem;
    height: 3.0rem;
    background-color: $yellow_color;
    border-radius: 50%;
    font-size: 1.6rem;
    line-height: 1;
    z-index: 3;
  }
}

