/* btn */
.btn_wrap {
  position: relative;
}

.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 20px;
  max-width: 240px;
  width: 100%;
  height: 50px;
  background: #F2F2F2;
  border: 1px solid #858585;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  color: #1A1A1A;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.4s;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
