  position: relative;
  color: inherit;
  text-decoration: none;
  z-index: 3;
}

.btn:hover {
  background: #A10707;
  border-color: #A10707;
  color: #FFFFFF;
}

.viewmore_link_wrap {
  position: relative;
}

.viewmore_link_wrap .viewmore_link {
  position: relative;
  display: inline-block;
  font-weight: bold;
  font-style: italic;
  font-size: 16px;
  line-height: 26px;
  color: #FF931E;
  text-decoration: none;
}

.viewmore_link_wrap .viewmore_link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FF931E;
}

.viewmore_link_wrap .viewmore_link::after {
  content: ">";
  display: inline-block;
  line-height: 1;
  margin-left: 6px;
  vertical-align: text-bottom;
}

.viewmore_link_wrap .viewmore_link:hover::before {
  width: 100%;
  animation: link-lineloop 0.6s ease-out;
  -webkit-animation: link-lineloop 0.6s ease-out;
}

.view-more {
  position: relative;
}

.link-btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.2rem 3.0rem;
  max-width: 100%;
  width: 27.4rem;
  height: 7.0rem;
  background: #FFE138;
  border: 0.3rem solid #FAD400;
  font-family: "Hiragino Mincho ProN", serif;
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 300;
  letter-spacing: 0.1em;
  white-space: nowrap;
  color: #000000;
  text-align: center;
  text-decoration: none;
  outline: none;
  border-radius: 3.5rem;
  transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  cursor: pointer;
  z-index: 1;
}

.link-btn span {
  position: relative;
  color: inherit;
  text-decoration: none;
  transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  z-index: 3;
}

.link-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.link-btn:hover {
  color: #000000;
}

.link-btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

