.mainvisual {
  position: relative;
  padding: 0;
  width: 100%;
  .main-picture {
    position: relative;
    max-width: 113.0rem;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    z-index: 0;
    @include media('tb') {
      max-width: 40.0rem;
    }
    @include media('sp') {
      max-width: 30.0rem;
    }
    img {
      width: auto;
      max-width: 100%;
      height: clamp(40.0rem, calc( ( var(--vh, 1vh) * 100 ) - 45.4rem ), 90.0rem);
      object-position: top center;
      @include media('tb') {
        height: clamp(41.2rem, calc( ( var(--vh, 1vh) * 100 ) - 43.2rem ), 72.0rem);
      }
      @include media('sp') {
        height: clamp(41.2rem, calc( ( var(--vh, 1vh) * 100 ) - 42.4rem ), 72.0rem);
      }
    }
  }
}

.section-intro {
  position: relative;
  padding: 5.0rem 0 5.0rem;
  z-index: 3;
  @include media('tb') {
    padding: 4.0rem 0 5.0rem;
  }
  .intro-card {
    position: relative;
    .icon {
      position: relative;
      width: 9.7rem;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 4.0rem;
      @include media('sp') {
        margin-bottom: 3.6rem;
      }
    }
    .title {
      position: relative;
      font-weight: 300;
      font-size: 2.0rem;
      letter-spacing: 0.1em;
      line-height: 3.2rem;
      text-align: center;
      color: #000;
      margin-bottom: 3.0rem;
      @include media('sp') {
        margin-bottom: 2.4rem;
      }
    }
    .text {
      position: relative;
      font-weight: 300;
      font-size: 1.4rem;
      letter-spacing: 0.1em;
      line-height: 2.4rem;
      text-align: center;
      color: #000;
      @include media('tb') {
        font-size: 1.2rem;
        line-height: 2.0rem;
      }
    }
    .link-btn {
      position: relative;
      margin-top: 5.0rem;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.page-action-btn {
  position: sticky;
  top: calc( 100vh - 12.0rem );
  left: calc(50% - 13.6rem);
  box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
  margin: 0;
  z-index: 99;
  @include media('sp') {
    top: calc( 100vh - 11.0rem );
  }
}

.section-product {
  padding: 8.0rem 0;
  @include media('tb') {
    padding: 6.0rem 0 7.0rem;
  }
  @include media('sp') {
    padding: 5.0rem 0 6.0rem;
    .wrapper {
      padding: 0;
    }
  }
  .product-item-inline {
    position: relative;
    display: block;
    padding: 0;
    &::after {
      content: "";
      visibility: hidden;
      height: 0;
      display: block;
      font-size: 0;
      clear: both;
    }
    .thumb {
      position: relative;
      width: 60%;
      float: left;
      @include media('tb') {
        width: 100%;
        float: none;
        margin-bottom: 4.0rem;
      }
      .image {
        position: relative;
        width: 100%;
        @include media('tb') {
          max-width: 40.0rem;
          margin-left: auto;
          margin-right: auto;
        }
      }
    }
    .head {
      position: relative;
      width: calc(40% - 5.0rem);
      float: right;
      margin-bottom: 4.0rem;
      @include media('tb') {
        width: 100%;
        float: none;
      }
      @include media('sp') {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
      }
      .title {
        position: relative;
        font-weight: 300;
        font-size: 2.0rem;
        letter-spacing: 0.1em;
        line-height: 3.2rem;
        text-align: center;
        margin-bottom: 2.4rem;
        @include media('sp') {
          font-size: 1.8rem;
          line-height: 3.0rem;
          margin-bottom: 3.0rem;
        }
      }
      .price {
        position: relative;
        display: block;
        font-size: 1.4rem;
        letter-spacing: 0.1em;
        line-height: 2.4rem;
        text-align: center;
        max-width: 20.0rem;
        width: 100%;
        padding: 0.8rem 1.0rem;
        border-top: 1px solid #000000;
        border-bottom: 1px solid #000000;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .content {
      position: relative;
      width: calc(40% - 5.0rem);
      float: right;
      @include media('tb') {
        width: 100%;
        float: none;
      }
      @include media('sp') {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
      }
      .text {
        position: relative;
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
        text-align: center;
      }
      .meta {
        position: relative;
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
        text-align: left;
        padding: 1.2rem 0 1.2rem 2.4rem;
        max-width: 32.0rem;
        width: 100%;
        border-top: 1px solid #000000;
        border-bottom: 1px solid #000000;
        margin-left: auto;
        margin-right: auto;
        margin-top: 5.0rem;
        @include media('tb') {
          margin-top: 5.0rem;
        }
        @include media('sp') {
          margin-top: 4.0rem;
        }
      }
      .marks {
        position: relative;
        list-style: none;
        display: grid;
        grid-template-columns: repeat(2, minmax(0px, 1fr));
        grid-column-gap: 1.6rem;
        grid-row-gap: 1.6rem;
        padding: 0;
        width: 100%;
        margin-top: 5.0rem;
        @include media('sp') {
          grid-column-gap: 1.2rem;
          grid-row-gap: 1.2rem;
          max-width: 35.0rem;
          margin-left: auto;
          margin-right: auto;
          margin-top: 4.0rem;
        }
        & > li {
          position: relative;
        }
        .mark-link-item {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          padding: 1.0rem 1.0rem 1.0rem 2.0rem;
          background: #EE8CAB;
          border-radius: 6px;
          box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
          text-decoration: none;
          outline: none;
          overflow: hidden;
          z-index: 1;
          .icon {
            flex: 0 0 auto;
            width: 3.8rem;
          }
          .text {
            flex: 0 0 auto;
            width: calc(100% - 4.5rem);
            line-height: 1;
            padding-bottom: 0.2rem;
            strong {
              position: relative;
              display: block;
              font-family: $font_mincho;
              font-weight: 300;
              font-size: 2.0rem;
              line-height: 3.0rem;
              letter-spacing: 0.1em;
              text-align: center;
              color: #FFFFFF;
              text-decoration: none;
            }
            small {
              position: relative;
              display: block;
              font-family: $font_mincho;
              font-weight: 300;
              font-size: 1.0rem;
              line-height: 1.6rem;
              letter-spacing: 0.1em;
              text-align: center;
              color: #FFFFFF;
              text-decoration: none;
            }
          }
        }
        .spring {
          background: #EE8CAB;
        }
        .summer {
          background: #FFB634;
        }
        .autumn {
          background: #F9845B;
        }
        .winter {
          background: #51B3E0;
        }
      }
    }
  }
}

#price {
  padding: 10.0rem 0 8.0rem;
  @include media('tb') {
    padding: 8.0rem 0 7.0rem;
  }
  @include media('sp') {
    padding: 7.0rem 0 6.0rem;
  }
}

.section-luxurious {
  position: relative;
  padding: 8.0rem 0 4.0rem;
  @include media('tb') {
    padding: 6.0rem 0 4.0rem;
  }
  @include media('sp') {
    padding: 5.0rem 0 3.0rem;
  }
 
  .luxurious-wrap-inline {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    @include media('tb') {
      flex-direction: column-reverse;
    }
    .phone {
      position: relative;
      flex: 0 0 auto;
      width: fit-content;
      width: 48%;
      @include media('tb') {
        width: 100%;
      }
      .image {
        position: relative;
        width: 100%;
        max-width: 36.6rem;
        margin-left: auto;
        margin-right: auto;
      }
      .help {
        position: relative;
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
        text-align: center;
        margin-top: 2.0rem;
      }
    }
    .card {
      position: relative;
      flex: 0 0 auto;
      width: fit-content;
      width: calc(52% - 5.0rem);
      @include media('tb') {
        width: 100%;
        margin-bottom: 5.0rem;
      }
      .title {
        position: relative;
        font-size: 3.0rem;
        letter-spacing: 0.1em;
        line-height: 4.5rem;
        text-align: center;
        margin-bottom: 4.5rem;
        @include media('tb') {
          font-size: 2.6rem;
          line-height: 4.0rem;
          margin-bottom: 4.0rem;
        }
        @include media('sp') {
          font-size: 2.0rem;
          line-height: 3.2rem;
        }
      }
      .text {
        position: relative;
        font-size: 1.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.1em;
        text-align: center;
        @include media('tb') {
          font-size: 1.2rem;
          line-height: 2.0rem;
        }
      }
    }
  }
}

