.section-operation {
  position: relative;
  padding: 8.0rem 0;
  background: #F7F7F7;
  @include media('tb') {
    padding: 7.0rem 0 7.0rem;
  }
  @include media('sp') {
    padding: 6.0rem 0 6.0rem;
  }
  .operation-box {
    position: relative;
    max-width: 86.8rem;
    width: 100%;
    background: #FFFFFF;
    padding: 6.0rem 5.0rem;
    box-shadow: 0 3px 6px rgba($color: #000000, $alpha: 0.1);
    margin-left: auto;
    margin-right: auto;
    @include media('tb') {
      max-width: 40.0rem;
      padding: 4.0rem;
      margin-left: auto;
      margin-right: auto;
    }
    @include media('sp') {
      padding: 4.0rem 3.0rem;
    }
    .box-title {
      position: relative;
      font-size: 2.0rem;
      letter-spacing: 0.1em;
      line-height: 3.2rem;
      text-align: center;
      margin-bottom: 3.0rem;
    }
    .operations {
      position: relative;
      list-style: none;
      width: fit-content;
      max-width: 100%;
      padding: 0;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 6.0rem;
      @include media('tb') {
        margin-bottom: 4.5rem;
      }
      .item {
        position: relative;
        &:not(:last-child) {
          margin-bottom: 4.5rem;
        }
        .label {
          position: relative;
          font-size: 1.4rem;
          line-height: 2.4rem;
          letter-spacing: 0.1em;
          margin-bottom: 1.2rem;
        }
        .text {
          position: relative;
          font-size: 1.4rem;
          line-height: 2.4rem;
          letter-spacing: 0.1em;
          padding-left: 1.6rem;
        }
      }
      
    }
    .price-item {
      position: relative;
      .price {
        position: relative;
        font-size: 1.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.1em;
        text-align: center;
        margin-bottom: 1.2rem;
      }
      .text {
        position: relative;
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
        text-align: center;
      }
    }

    .box-help {
      position: relative;
      list-style-type: "※";
      font-size: 1.2rem;
      line-height: 2.0rem;
      letter-spacing: 0.1em;
      padding-left: 1.4rem;
      margin-top: 6.0rem;
      @include media('sp') {
        margin-top: 4.5rem;
      }
    }
    
  }
}

.section-term {
  position: relative;
  padding: 8.0rem 0 2.0rem;
  @include media('tb') {
    padding: 6.0rem 0 4.0rem;
  }
  @include media('tb') {
    padding: 5.0rem 0 3.0rem;
  }
  .term-list {
    position: relative;
    list-style: none;
    width: 100%;
    padding: 0;
    .term-item {
      position: relative;
      width: fit-content;
      max-width: 100%;
      min-width: 45.0rem;
      padding: 0;
      margin-left: auto;
      margin-right: auto;
      @include media('sp') {
        min-width: auto;
        max-width: 40.0rem;
      }
      &:not(:last-child) {
        margin-bottom: 8.0rem;
        @include media('sp') {
          margin-bottom: 6.0rem;
        }
      }
      .title {
        position: relative;
        font-size: 2.8rem;
        letter-spacing: 0.1em;
        line-height: 4.2rem;
        text-align: center;
        margin-bottom: 5.0rem;
        @include media('tb') {
          font-size: 2.0rem;
          line-height: 3.2rem;
          margin-bottom: 3.0rem;
        }
        @include media('tb') {
          margin-bottom: 2.4rem;
        }
      }
      ul.text {
        position: relative;
        list-style-type: "・";
        padding-left: 1.4rem;
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
      }
      .help {
        font-size: 1.2rem;
        line-height: 2.0rem;
        letter-spacing: 0.1em;
        padding-left: 1.4rem;
      }
    }
  }
}

.section-contact {
  position: relative;
  padding: 8.0rem 0 0.0rem;
  @include media('tb') {
    padding: 6.0rem 0 0.0rem;
  }
  @include media('sp') {
    padding: 5.0rem 0 0.0rem;
  }
  .contact-index {
    position: relative;
    .help {
      position: relative;
      font-size: 1.2rem;
      line-height: 2.0rem;
      letter-spacing: 0.1em;
      text-align: center;
      margin-bottom: 6.0rem;
      @include media('sp') {
        margin-bottom: 5.0rem;
      }
    }
    .lead {
      position: relative;
      font-size: 1.8rem;
      line-height: 3.0rem;
      letter-spacing: 0.1em;
      text-align: center;
      @include media('sp') {
        font-size: 1.4rem;
        line-height: 2.4rem;
      }
      a {
        color: inherit;
        text-decoration: underline;
      }
    }
  }
}

.section-category {
  position: relative;
  padding: 2.0rem 0;
  @include media('sp') {
    padding: 2px 0;
  }
  .category-menu {
    position: relative;
    list-style: none;
    display: block;
    padding: 0;
    & > li {
      position: relative;
      &:not(:last-child) {
        margin-bottom: 1.2rem;
        @include media('sp') {
          margin-bottom: 2px;
        }
      }
    }
    .category-link {
      position: relative;
      display: block;
      width: 100%;
      padding: 3.6rem 3.0rem 3.8rem;
      background: #131313;
      text-decoration: none;
      outline: none;
      border: none;
      margin: 0;
      transition: all 0.4s ease-in-out;
      @include media('sp') {
        padding: clamp(3.0rem, calc( ( ( var(--vh, 1vh) * 100 ) - 41.6rem ) / 8.0 ), 6.0rem) 1.5rem;
      }
      &:hover {
        opacity: 0.7;
      }
      .jp {
        position: relative;
        font-family: $font_mincho;
        font-weight: 300;
        font-size: 4.0rem;
        line-height: 1.2;
        letter-spacing: 0.1em;
        text-align: center;
        color: #FFFFFF;
        text-decoration: none;
      }
      .en {
        position: relative;
        font-family: $font_mincho;
        font-weight: 300;
        font-size: 1,2rem;
        line-height: 1.8rem;
        letter-spacing: 0.1em;
        text-align: center;
        color: #FF82A7;
        margin-top: 1.2rem;
        text-decoration: none;
      }
      &.pink {
        .jp {
          .colored {
            color: #FF4573;
          }
        }
        .en {
          color: #FF82A7;
        }
      }
      &.yellow {
        .jp {
          .colored {
            color: #FBB62D;
          }
        }
        .en {
          color: #FBB62D;
        }
      }
      &.coral {
        .jp {
          .colored {
            color: #FD885B;
          }
        }
        .en {
          color: #FD885B;
        }
      }
      &.cyan {
        .jp {
          .colored {
            color: #47C0FB;
          }
        }
        .en {
          color: #47C0FB;
        }
      }
    }
  }
}