.mobile-nav-toggle {
  position: fixed;
  right: 2.0rem;
  top: 2.5rem;
  width: 5.0rem;
  height: 5.0rem;
  padding: 0;
  background: transparent;
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 999;
  margin: 0;
  transition: all 0.4s ease-in-out;
  @include media('tb') {
    display: inline-block;
  }
  @include media('sp') {
    top: 2.0rem;
  }
  .toggle-icon {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    span {
      display: inline-block;
      position: absolute;
      right: 0;
      width: 100%;
      height: 1px;
      background: #000000;
      transition: all 0.4s ease-in-out;
      transform-origin: center center;
      &:nth-child(1) {
        top: calc(50% - 6px);
      }
      &:nth-child(2) {
        top: calc(50% + 6px);
        width: 80%;
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
  }
  &.toggle-active {
    .toggle-icon {
      span {
        background: #FFFFFF;
        &:nth-child(1) {
          transform: translateY(7px) rotate(-225deg);
          width: 100%;
        }
        &:nth-child(2) {
          transform: translateY(-6px) rotate(-135deg);
          width: 100%;
        }
        &:nth-child(3) {
          opacity: 0;
          visibility: hidden;
        }
        
      }
    }
  }
}

body.bg-black {
  .mobile-nav-toggle .toggle-icon span {
    background: #FFFFFF;
  }
}

// Mobile Nav
#mobile-nav {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 998;
  right: -100%;
  width: 100%;
  overflow-y: auto;
  transition: all 0.4s ease-in-out;
  background: transparent;
  backdrop-filter: blur(50px);
  --webkit-backdrop-filter: blur(50px);
  background-color: rgba($color: #000000, $alpha: 0.3);
  .nav-body {
    position: relative;
    padding: 18.0rem 3.0rem 6.0rem;
    @include media('sp') {
      padding: 15.0rem 1.5rem 6.0rem;
      padding: clamp(12.0rem, 24vh, 18.0rem) 1.5rem 6.0rem;
    }
  }
  .mobile-nav-menu {
    position: relative;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    & > li {
      position: relative;
      &:not(:last-child) {
        margin-bottom: 3.0rem;
      }
    }
    .menu-link {
      position: relative;
      display: inline-block;
      font-family: $font_mincho;
      font-weight: 300;
      font-size: 2.0rem;
      line-height: 3.2rem;
      text-align: center;
      color: #FFFFFF;
      padding: 0;
      text-decoration: none;
      outline: none;
      @include media('sp') {
        font-size: 1.8rem;
        line-height: 3.0rem;
      }
    }
    .cart-link-btn {
      position: relative;
      overflow: hidden; 
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 1.2rem 3.0rem;
      max-width: 100%;
      width: 15.6rem;
      height: 5.0rem;
      background: #FFE138;
      border: 2px solid #FFE138;
      font-family: $font_mincho;
      font-size: 1.4rem;
      line-height: 2.4rem;
      font-weight: 300;
      letter-spacing: 0.1em;
      white-space: nowrap;
      color: #000000;
      text-align: center;
      text-decoration: none;
      outline: none;
      border-radius: 2.5rem;
      transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
      cursor: pointer;
      box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
      z-index: 1;
      margin-left: auto;
      margin-right: auto;
      span {
        position: relative;
        color: inherit;
        text-decoration: none;
        transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        z-index: 3;
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        background: #FFFFFF;
        width: 100%;
        height: 100%;
        transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        transform-origin: right top;
        transform: scale(0, 1);
        z-index: 1;
      }
      &:hover {
        color: #000000;
        &::before {
          transform-origin: left top;
          transform: scale(1, 1);
        }
      }
    }
  }
}

.mobile-nav-overly {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 997;
  background: rgba($color: #000000, $alpha: 0.5);
  display: none;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
}


/* Mobile Nav body classes */
body.mobile-nav-active {
  @include media('sp') {
    overflow: hidden;
  }
}
body.mobile-nav-active #mobile-nav {
  right: 0;
}