#header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2.0rem 6.0rem 1.9rem 5.0rem;
  background: transparent;
  backdrop-filter: blur(30px);
  --webkit-backdrop-filter: blur(30px);
  background-color: rgba($color: #FFFFFF, $alpha: 0.15);
  z-index: 996;
  @include media('lb') {
    padding-left: 3.0rem;
    padding-right: 3.0rem;
  }
  @include media('tb') {
    padding-left: 2.0rem;
    padding-right: 2.0rem;
  }
  @include media('sp') {
    padding: 1.9rem 2.0rem 1.8rem;
    height: 9.0rem;
  }
  &.header-scrolled {
    box-shadow: 0 3px 60px rgba($color: #000000, $alpha: 0.16);
  }
  .header-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    .logo {
      position: relative;
      .image {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 6.4rem;
        text-decoration: none;
        outline: none;
        padding: 0;
        margin: 0;
        @include media('sp') {
          width: 5.5rem;
        }
        img {
         width: 100%; 
        }
      }
    }
    .nav {
      position: relative;
      padding: 0;
      @include media('tb') {
        display: none;
      }
    }
    .nav-menu {
      position: relative;
      list-style: none;
      display: flex;
      align-items: center;
      line-height: 1;
      padding: 0;
      & > li {
        position: relative;
        padding: 0;
        &:not(:first-child) {
          margin-left: 6.0rem;
        }
      }
      .menu-link {
        position: relative;
        display: inline-block;
        font-family: $font_mincho;
        font-size: 1.6rem;
        font-weight: 300;
        line-height: 2.4rem;
        color: #000000;
        text-align: center;
        padding: 0;
        margin: 0;
        white-space: nowrap;
        text-decoration: none;
        cursor: pointer;
        transition: all 0.2s ease-in-out;
        z-index: 1;
        span {
          color: inherit;
          text-decoration: none;
        }
        &:hover {
          opacity: 0.7;
        }
      }
      .cart-link-btn {
        position: relative;
        overflow: hidden; 
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1.2rem 3.0rem;
        max-width: 100%;
        width: 15.6rem;
        height: 5.0rem;
        background: #FFE138;
        border: 2px solid #FFE138;
        font-family: $font_mincho;
        font-size: 1.4rem;
        line-height: 2.4rem;
        font-weight: 300;
        letter-spacing: 0.1em;
        white-space: nowrap;
        color: #000000;
        text-align: center;
        text-decoration: none;
        outline: none;
        border-radius: 2.5rem;
        transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
        cursor: pointer;
        box-shadow: 0px 3px 6px rgba($color: #000000, $alpha: 0.16);
        z-index: 1;
        span {
          position: relative;
          color: inherit;
          text-decoration: none;
          transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
          z-index: 3;
        }
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          background: #FFFFFF;
          width: 100%;
          height: 100%;
          transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
          transform-origin: right top;
          transform: scale(0, 1);
          z-index: 1;
        }
        &:hover {
          color: #000000;
          &::before {
            transform-origin: left top;
            transform: scale(1, 1);
          }
        }
      }
    }
  }
}

body.bg-black {
  #header {
    background: #FFFFFF;
    @include media('tb') {
      background: #131313;
    }
  }
}

#main-nav {
  position: relative;
  width: 100%;
  background: $yellow_color;
  padding: 1.0rem 1.5rem;
  z-index: 9;
  @include media('tb') {
    display: none;
  }
  .nav-menu {
    position: relative;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    & > li {
      position: relative;
      padding: 0 2.0rem;
      & > a {
        position: relative;
        display: inline-block;
        font-family: $font_noto;
        font-size: 1.6rem;
        font-weight: 600;
        line-height: 1.5;
        color: #000000;
        padding: 0;
        margin: 0;
        white-space: nowrap;
        text-decoration: none;
        cursor: pointer;
        z-index: 0;
        &:hover {
          text-decoration: underline;
        }
      }
    }
    .drop_down {
      ul {
        display: block;
        list-style: none;
        position: absolute;
        left: 0;
        top: calc(100% + 20px);
        z-index: 99;
        opacity: 0;
        visibility: hidden;
        padding: 0;
        transition: 0.3s ease-in-out;
        li {
          position: relative;
          min-width: 200px;
          padding: 0;
          &:not(:last-child) {
            border-bottom: 1px solid #808080;
          }
        }
        a {
          position: relative;
          display: block;
          font-size: 14px;
          font-weight: normal;
          line-height: 22px;
          color: #FFFFFF;
          padding: 12px 3.57rem;
          margin: 0;
          background: rgba($color: #1A1A1A, $alpha: 0.8);
          white-space: nowrap;
          text-decoration: none;
          cursor: pointer;
          transition: all 0.3 ease-in-out;
          z-index: 0;
          &:hover {
            background: #1A1A1A;
            color: #FFFFFF;
          }
        }
        .drop_down ul {
          top: 0;
          left: calc(100% - 30px);
        }
        
        .drop-down:hover > ul {
          opacity: 1;
          top: 0;
          left: 100%;
        }
      }
      &:hover {
        & > ul {
          opacity: 1;
          top: 100%;
          visibility: visible;
        }
      }
    }
  }
}
