// pc&sp
.pc {
  display: block !important;
}

.sp {
  display: none !important;
}

.tc {
  display: block !important;
}

.tb {
  display: none !important;
}

@include media('tb') {
  .tc {
    display: none !important;
  }

  .tb {
    display: block !important;
  }
}

@include media('sp') {
  .pc {
    display: none !important;
  }

  .sp {
    display: block !important;
  }
}

//hide

@include media('pc') {
  .pc-hide {
    display: none !important;
  }
}

@include media('sp') {
  .sp-hide {
    display: none !important;
  }
}

// wrapper
.wrapper {
  position: relative;
  max-width: 125.0rem;
  width: 100%;
  padding-left: 6.0rem;
  padding-right: 6.0rem;
  margin-left: auto;
  margin-right: auto;
  @include media('lb') {
    padding-left: 3.0rem;
    padding-right: 3.0rem;
  }
  @include media('tb') {
    padding-left: 2.0rem;
    padding-right: 2.0rem;
  }
  @include media('sp') {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

main {
  position: relative;
}

/* input */
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #FFFFFF;
  border: 1px solid #CECECE;
  box-sizing: border-box;
  font-family: $font_noto;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 2.4rem;
  text-align: left;
  color: $base_color;
  outline: none !important;
  padding: 1.2rem 1.5rem;
  width: 100%;
  transition: all 0.3s;
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 4.8rem;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #275BC8;
}

// picture
picture,
figure,
.image_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

// scroll
.scroll {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: auto;
}


