#footer {
  position: relative;
  padding: 6.0rem 0 17.0rem;
  margin: 0;
  @include media('sp') {
    padding: 5.0rem 0 15.0rem;
  }
  .footer-nav {
    position: relative;
    margin-bottom: 6.0rem;
    @include media('sp') {
      margin-bottom: 5.0rem;
    }
    .nav-menu {
      position: relative;
      display: block;
      list-style: none;
      text-align: center;
      padding: 0;
      & > li {
        position: relative;
        &:not(:last-child) {
          margin-bottom: 1.2rem;
          @include media('sp') {
            margin-bottom: 1.0rem;
          }
        }
        & > a {
          position: relative;
          font-family: $font_mincho;
          font-size: 1.2rem;
          font-weight: 300;
          line-height: 2.0rem;
          text-decoration: underline;
          outline: none;
          color: #000000;
        }
      }
      
    }
  }

  .footer-text {
    position: relative;
    font-size: 1.2rem;
    line-height: 2.4rem;
    text-align: center;
  }
  
  .copyright {
    margin-top: 3.0rem;
    font-size: 1.2rem;
    line-height: 2.0rem;
    text-align: center;
  }
}

.back_to_top {
  position: fixed;
  bottom: 30px;
  right: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  z-index: 996;
  @include media('sp') {
    width: 45px;
    right: 16px;
    bottom: 16px;
  }
  img {
    width: 100%;
    @include media('pc') {
      transition: all 0.4s;
    }
  }
  @include media('pc') {
    &:hover {
      text-decoration: none;
      img {
        filter: drop-shadow(0 0 12px #fff);
        -webkit-filter: drop-shadow(0 0 12px #fff);
      }
    }
  }
}
